import 'foundation-sites/js/foundation.core';
import 'foundation-sites/js/foundation.util.mediaQuery';
import 'foundation-sites/js/foundation.util.timerAndImageLoader';
import 'foundation-sites/js/foundation.equalizer';

!function ($) {
    "use strict";

    // Initialize
    $.each($('[data-equalizer]'), function(key, obj){
        var equalizerName = $(obj).data('equalizer');
        var equalizer = new Foundation.Equalizer($(obj));

        // Register this module
        WayneState.register('equalizer-' + (equalizerName != '' ? equalizerName : ''), equalizer);
    });
}(jQuery);



