import 'foundation-sites/js/foundation.core';
import 'foundation-sites/js/foundation.util.mediaQuery';
import 'foundation-sites/js/foundation.util.triggers';
import 'foundation-sites/js/foundation.util.motion';
import 'foundation-sites/js/foundation.offcanvas';

!function ($) {
    "use strict";

    /**
     * Accordion module.
     */
    class OffCanvas {

        constructor() {
            this._init();
        }

        /**
         *  For resizing the browser
         *  Selects the UL that contains the button link for offcanvas and toggle the class for off-canvas
         *  Close offCanvas because it may need to be hidden based on the breakpoint you are on
         */
        toggle() {
            // Close offcanvas
            WayneState.modules.foundationOffCanvas.close();

            // Toggle the appropriate classes for offCanvas
            if ($('.menu-top-container .menu-top.hide-for-menu-top-up').is(":visible")) {
                $('#mainMenu').addClass('off-canvas position-right');
            } else {
                $('#mainMenu').removeClass('off-canvas position-right');
            }
        }

        mainMenuToggle() {
            $('.offcanvas-main-menu a.main-menu').prepend('<span class="icon-right-open-mini expand-icons"></span>');
            $('.main-menu h2 a').prepend('<span class="icon-down-open-mini expand-icons"></span>');

            $('.offcanvas-main-menu a.main-menu').click(function() {
                // Show/hide the main menu
                $('.offcanvas-main-menu ul ul').toggle();

                if($('.offcanvas-main-menu ul ul').is(':visible')) {
                    $('.offcanvas-main-menu a.main-menu .expand-icons').toggleClass('icon-right-open-mini icon-down-open-mini');
                }else{
                    $('.offcanvas-main-menu a.main-menu .expand-icons').toggleClass('icon-down-open-mini icon-right-open-mini');
                }

                // Return false so it does not complete the click through
                return false;
            });
        }

        /**
         * Initialize
         */
        _init() {
            // Make sure offcanvas is closed from the start
            WayneState.modules.foundationOffCanvas.close();

            // Redo offCanvas because of the random hash it creates.
            var foundationOffCanvas = new Foundation.OffCanvas($('[data-off-canvas]'));
            WayneState.register('foundationOffCanvas', foundationOffCanvas);

            // Make the click link for offcanvas button do nothing, this way #mainMenu doesn't append to the URL
            $('.menu-top-container .menu-icon').attr('href', 'javascript:void(0);');

            // Initialize offcanvas toggle classes
            this.toggle();

            // Toggle the Main Menu
            this.mainMenuToggle();

            // When resizing the browse
            $(window).resize(this.toggle);
        }
    }

    // Initialize and register Foundation's offCanvas
    var foundationOffCanvas = new Foundation.OffCanvas($('[data-off-canvas]'));
    WayneState.register('foundationOffCanvas', foundationOffCanvas);

    // Initialize and register offCanvas
    var offcanvas = new OffCanvas();
    WayneState.register('offcanvas', offcanvas);

}(jQuery);



