import 'slick-carousel/slick/slick.js';

!function ($) {
    "use strict";

    /**
     * Slick module.
     */
    class Slick {

        constructor() {
            this._init();
        }

        /**
         * Initialize
         */
        _init() {
            // Slick Config
            var config = {
                dots: false
            };

            // Slick_dept Config
            var config_dept = {
                dots: true,
                arrows: false
            };

            // Lazyload images
            $('img.b-lazy').each(function () {
                // Replace the data-src with the real src
                $(this).attr('src', $(this).attr('data-src'));
            });

            // Replace all src with data-src
            $('div.b-lazy').each(function(){
                // Replace the data-src with the real src
                $(this).css('background-image', $(this).attr('data-src'));
            });

            // Initialize Slick
            $('.slick').slick(config);
            $('.slick_dept').slick(config_dept);
        }
    }

    // Initialize
    var slick = new Slick();

    // Register this module
    WayneState.register('slick', slick);

}(jQuery);



